.nav-bar {
  display: block;
  margin: 0 auto;
}

.nav-bar ul{
  list-style: none;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.nav-bar ul li{
  margin: 2em;
}

.link{
  text-decoration: none;
  color: white;
  background-color: grey;
  padding: 4em;
  transition: 1s;
}

.link:hover {
  background-color: aliceblue;
  color: black;
}